<template>
  <div class="game">
    <header>
      <h1 class="title">2048</h1>
      <div class="nav">
        <div class="btn" @click="startGame">开始游戏</div>
        <div class="score">
          <span>分数：</span>
          <span>{{ score }}</span>
        </div>
      </div>
    </header>
    <div class="borad">
      <div class="rows" v-for="(row, r_i) in array" :key="'row-' + r_i">
        <div
          :style="{ 'background-color': switchCellColor(col) }"
          :class="['cell', r_i + '-' + c_i]"
          v-for="(col, c_i) in row"
          :key="'cell-' + r_i + '-' + c_i"
        >
          {{ col != 0 ? col : "" }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "game",

  data() {
    return {
      score: 0,
      array: [],
    };
  },
  mounted() {
    this.initGame();
    document.addEventListener("keydown", this.getKeyboardEvent);
  },
  methods: {
    startGame() {
      this.score = 0;
      this.initGame();
    },
    initGame() {
      /*
       * 游戏初始化
       *  - 初始化棋盘
       *  - 随机生成一个数字
       **/
      this.array = Array(4)
        .fill(0)
        .map(() => Array(4).fill(0));
      for (let i = 0; i < 2; i++) {
        this.cell(this);
      }
    },
    cell(that) {
      function getRandomValue() {
        /* 获取随机数：2/4 */
        return Math.random() <= 0.5 ? 2 : 4;
      }
      function getRandomPosition(array) {
        /* 获取棋盘的随机位置 */
        let empty_list = [];
        array.forEach((value, row) => {
          value.forEach((v, column) => {
            if (v == 0) empty_list.push([row, column]);
          });
        });
        return empty_list[
          parseInt(Math.floor(Math.random() * empty_list.length))
        ];
      }
      try {
        let [x, y] = getRandomPosition(that.array);
        that.$set(this.array[x], y, getRandomValue());
        return true;
      } catch (err) {
        return false;
      }
    },
    gameOver() {},

    getBlockHorizontal(row, col1, col2, array) {
      for (var i = col1 + 1; i < col2; i++)
        if (array[row][i] != 0) return false;
      return true;
    },
    getBlockVertical(col, row1, row2, array) {
      for (var i = row1 + 1; i < row2; i++)
        if (array[i][col] != 0) return false;
      return true;
    },

    switchCellColor(num) {
      switch (num) {
        case 2:
          return "#eee4da";
          break;
        case 4:
          return "#ede0c8";
          break;
        case 8:
          return "#f2b179";
          break;
        case 16:
          return "#f59563";
          break;
        case 32:
          return "#f67c5f";
          break;
        case 64:
          return "#f65e3b";
          break;
        case 128:
          return "#edcf72";
          break;
        case 256:
          return "#edcc61";
          break;
        case 512:
          return "#9c0";
          break;
        case 1024:
          return "#33b5e5";
          break;
        case 2048:
          return "#09c";
          break;
        case 4096:
          return "#a6c";
          break;
        case 8192:
          return "#93c";
          break;
      }
    },
    getKeyboardEvent(e) {
      switch (e.keyCode) {
        case 37:
          /* 左 */
          this.moveLeft();
          break;
        case 38:
          /* 上 */
          this.moveUp();
          break;
        case 39:
          /* 右 */
          this.moveRight();
          break;
        case 40:
          /* 下 */
          this.moveDown();
          break;
      }
      let status = this.cell(this);
      if (!status && this.noMove(this.array)) alert("游戏结束");
    },
    noMove(array) {
      if (
        this.canMoveLeft(array) ||
        this.canMoveRight(array) ||
        this.canMoveUp(array) ||
        this.canMoveDown(array)
      )
        return false;
      return true;
    },
    canMoveLeft(array) {
      for (var i = 0; i < 4; i++)
        for (var j = 0; j < 4; j++)
          if (array[i][j] != 0 && j != 0)
            if (array[i][j - 1] == 0 || array[i][j - 1] == array[i][j])
              return true;
      return false;
    },
    canMoveRight(array) {
      for (var i = 0; i < 4; i++)
        for (var j = 0; j < 4; j++)
          if (array[i][j] != 0 && j != 3)
            if (array[i][j + 1] == 0 || array[i][j + 1] == array[i][j])
              return true;
      return false;
    },
    canMoveUp(array) {
      for (var i = 0; i < 4; i++)
        for (var j = 0; j < 4; j++)
          if (array[i][j] != 0 && i != 0)
            if (array[i - 1][j] == 0 || array[i - 1][j] == array[i][j])
              return true;
      return false;
    },
    canMoveDown(array) {
      for (var i = 0; i < 4; i++)
        for (var j = 0; j < 4; j++)
          if (array[i][j] != 0 && i != 3)
            if (array[i + 1][j] == 0 || array[i + 1][j] == array[i][j])
              return true;
      return false;
    },
    moveLeft() {
      let status = this.canMoveLeft(this.array);
      if (!status) return false;
      for (var i = 0; i < 4; i++) {
        for (var j = 1; j < 4; j++) {
          if (this.array[i][j] != 0) {
            for (var k = 0; k < j; k++) {
              if (
                this.array[i][k] == 0 &&
                this.getBlockHorizontal(i, k, j, this.array)
              ) {
                this.array[i][k] = this.array[i][j];
                this.array[i][j] = 0;
                continue;
              } else if (
                this.array[i][k] == this.array[i][j] &&
                this.getBlockHorizontal(i, k, j, this.array)
              ) {
                this.array[i][k] += this.array[i][j];
                this.array[i][j] = 0;
                this.score += this.array[i][k];
                continue;
              }
            }
          }
        }
      }
      return true;
    },

    moveRight() {
      let status = this.canMoveRight(this.array);
      if (!status) return false;
      for (var i = 0; i < 4; i++)
        for (var j = 2; j >= 0; j--) {
          if (this.array[i][j] != 0) {
            for (var k = 3; k > j; k--) {
              if (
                this.array[i][k] == 0 &&
                this.getBlockHorizontal(i, j, k, this.array)
              ) {
                this.array[i][k] = this.array[i][j];
                this.array[i][j] = 0;
                continue;
              } else if (
                this.array[i][k] == this.array[i][j] &&
                this.getBlockHorizontal(i, j, k, this.array)
              ) {
                this.array[i][k] += this.array[i][j];
                this.array[i][j] = 0;
                this.score += this.array[i][k];
                continue;
              }
            }
          }
        }
    },
    moveUp() {
      let status = this.canMoveUp(this.array);
      if (!status) return false;
      for (var j = 0; j < 4; j++) {
        for (var i = 1; i < 4; i++) {
          if (this.array[i][j] != 0) {
            for (var k = 0; k < i; k++) {
              if (
                this.array[k][j] == 0 &&
                this.getBlockVertical(j, k, i, this.array)
              ) {
                this.array[k][j] = this.array[i][j];
                this.array[i][j] = 0;
                continue;
              } else if (
                this.array[k][j] == this.array[i][j] &&
                this.getBlockVertical(j, k, i, this.array)
              ) {
                this.array[k][j] += this.array[i][j];
                this.array[i][j] = 0;
                this.score += this.array[k][j];
                continue;
              }
            }
          }
        }
      }
    },
    moveDown() {
      let status = this.canMoveDown(this.array);
      if (!status) return false;
      for (var j = 0; j < 4; j++) {
        for (var i = 2; i >= 0; i--) {
          if (this.array[i][j] != 0) {
            for (var k = 3; k > i; k--) {
              if (
                this.array[k][j] == 0 &&
                this.getBlockVertical(j, i, k, this.array)
              ) {
                this.array[k][j] = this.array[i][j];
                this.array[i][j] = 0;
                continue;
              } else if (
                this.array[k][j] == this.array[i][j] &&
                this.getBlockVertical(j, i, k, this.array)
              ) {
                this.array[k][j] += this.array[i][j];
                this.array[i][j] = 0;
                this.score += this.array[k][j];
                continue;
              }
            }
          }
        }
      }
    },
  },
};
</script>
<style scoped>
