  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
  line-height: 40px;
}
.nav .btn,
.nav .score {
  /* line-height: 40px; */
  padding: 0 20px;
  border-radius: 15px;
}
.nav .btn {
  background-color: #8f7a66;
  color: white;
}

.nav .score {
  background-color: #bbada0;
  color: white;
  font-size: 105%;
}
.borad {
  width: 400px;
  height: 400px;
  padding: 20px;
  background-color: #bbada0;
  border-radius: 7px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.borad .rows {
  width: 100%;
